/**************************LoginPage*****************************/
.Outline{
  height:700px;
  background-image:url(../public/Img/amumamum.PNG);
  background-position: center;
  background-repeat: no-repeat;
  animation: fadeIn .5s steps(25,end), flowing 3s steps(25,end) infinite;
}
.LoginMain{
  background-color: white;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  height: 370px;
  border: 1px solid black;
  border-radius: 20px;
    opacity: 0.8;
  animation:fadeIn 1s steps(50, end);
}
.Login_h1 {
  width: 80%;
  white-space: nowrap;
  margin: 0 auto;
  margin-top:13px;
  color: #292929;
  border-right: 2px solid transparent;
  overflow: hidden;
  animation: typing 0.5s steps(50, end), blink-caret .75s step-end infinite;
}
.Login_h3{
  margin-top:10px;
  color: #2559bb;
}
.subBtn{
  margin-top:20px
}
@keyframes typing {
  from { width: 0; }
  to { width: 80%; }
}
@keyframes blink-caret {
  from, to { box-shadow: 1px 0 0 0 transparent; }
  50% { box-shadow: 1px 0 0 0; }
}
@keyframes fadeIn {
  from{opacity: 0;}
  to{opacity: 0.8;}
}
@keyframes flowing{
  from,to{margin:10px 0 0 20px;}
  30%{margin:-10px 0 0 30px}
  60%{margin:-15px 0 10px 0}
}

