.slide{
  height: 600px;
  width: 250px;
  top: 200px;
  right: 0px;
  position: absolute;
  background-color: #fff;
  transition: 0.5s ease ;
  display: flex;
  padding: 10px;
  border-radius: 3px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  transform: translateX(300px);
  flex-direction: column;
}
.slide h2{
  display: flex;
  text-align: left;
}
.slide .body {
  width: 200px;
  height: 500px;
  display: flex;
  padding: 10px;
  align-items: center;
  flex-direction: column;
}
.recordinput{
  top: 200px;
  right: 0px;
  position: absolute;
  -webkit-appearance: none;
}
.recordP{
  border-bottom: 0.5px solid #babecc;
}
.toggle{
  width: 40px;
  height: 40px;
  top: 220px;
  right: 20px;
  position: absolute;
  background-color: #fff;
  z-index: 1;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.toggle .common{
  position: absolute;
  height: 2px;
  width: 20px;
  background-color: #292929;
  border-radius: 50px;
  transition: 0.3s ease;
}
.toggle .top-line{
  top: 30%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.toggle .middle-line{
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.toggle .bottom-line{
  top: 70%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.recordinput:checked ~ .toggle .top-line{
  left: 2px;
  top: 20px;
  width: 35px;
  transform: rotate(45deg);
}
.recordinput:checked ~ .toggle .bottom-line{
  left: 2px;
  top: 20px;
  width: 35px;
  transform: rotate(-45deg);
}
.recordinput:checked ~ .toggle .middle-line{
  opacity: 0;
  transform: translateX(100px);
}
.recordinput:checked ~ .slide{
  transform: translateX(0);
}
