/****************************************************************/
/*************************Note**********************************/
.Note_Container {
  margin: 10px 10px 10px 10px;
}
.Note_Title {
  position: relative;
  text-align: center;
}
.Note_Create {
  position: absolute;
  top: 15%;
  left: 25%;
  width: 650px;
  height: 650px;
  background-image: url(../public/Img/Note/Note_Box_Back.png);
  background-size: 95%;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 10px;
  z-index: 1000000;
}
#NoteBlock {
  visibility: hidden;
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #424141;
  opacity: 0.5;
}
.NoteEditBtn{
  display:flex;
}
.Note_Box {
  display: flex;
  float: left;
  width: 98%;
  height: 600px;
  margin: 10px;
}
.NoteContentTxt {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.Note_Box_Classify {
  width: 16%;
  height: 580px;
  text-align: center;
}
.Note_Box_Content {
  overflow-x: auto;
  height: 580px;
  width: 75%;
}
.AddSubClassify {
  transition-duration: 0.4s;
  height: 100%;
  width: 70px;
  text-align: center;
  line-height: 100%;
  border-radius: 20px;
}
.AddSubClassify:hover {
  transition-duration: 0.4s;
  background-color: whitesmoke;
}
.Note_CreateBtn {
  margin-top: 10px;
  margin-left: 100px;
}
.AddNote {
  transition-duration: 0.4s;
  height: 50px;
  text-align: center;
  border: 0.5px solid black;
  border-radius: 20px;
  background-color: #598eb1;
}
.AddNote:hover {
  transition-duration: 0.4s;
  background-color: #74b7e3;
}
.NoteDroppable {
  transition-duration: 0.4s;
  border-radius: 10px;
  display: flex;
  margin-bottom: 8px;
}
.NoteDroppable:hover {
  transition-duration: 0.4s;
  background-color: #002053;
}
.NoteDeleteIcon {
  transition-duration: 0.4s;
  border-radius: 0 20px 20px 0;
  padding: 13px 10px 0 10px;
}
.NoteDeleteIcon:hover {
  transition-duration: 0.4s;
  color: #002053;
}
.NoteEditClassify {
  border-radius: 10px;
  transition-duration: 0.4s;
}
.NoteEditClassify:hover {
  transition-duration: 0.4s;
  background-color: rgb(118, 117, 117);
  color: white;
}
.NoteDeleteClassify {
  border-radius: 10px;
  transition-duration: 0.4s;
}
.NoteDeleteClassify:hover {
  transition-duration: 0.4s;
  background-color: rgb(118, 117, 117);
  color: white;
}
.Note_h2 {
  font-size: 3em;
  margin-left:5%;
  margin-top:10%;
}
.Note_ShowImage{
  border:1px solid rgba(51, 51, 51, 0.248);
  border-radius: 5px;
  padding:10px;
  margin-top:5px;
  margin-left:10%;
  width:80%;
  height:120px;
}
