.SelfProfile_Title {
  font-size: 3em;
  margin: 30px 100px 0 0;
  color: #292929;
}
.SelfProfile_subTitle {
  color: #292929;
  margin-top: 70px;
  margin-left: 80px;
}
.SelfProfile_Text {
  margin: 15px 0 0 30px;
  color: #2559bb;
}
.SelfProfile_Stand {
  margin-left: 5%;
  display: flex;
}
.SelfProfile_Self {
  display: flex;
  margin-right: 30px;
  float: right;
}
.Block {
  visibility: hidden;
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #424141;
  opacity: 0.5;
}
.PasswordChange,
.EmailChange {
  visibility: hidden;
  position: absolute;
  z-index: 101;
  top: 10%;
  left: 33%;
  width: 500px;
  height: 500px;
  background-color: whitesmoke;
  border: 1px solid #424141;
  border-radius: 20px 20px;
}
.ChooseClass {
  visibility: hidden;
  position: absolute;
  z-index: 101;
  top: 10%;
  left: 15%;
  width: 1000px;
  height: 500px;
  background-color: whitesmoke;
  border: 1px solid #424141;
  border-radius: 20px 20px;
}

.PasswordChange_Title,
.EmailChange_Title,
.ChooseClass_Title {
  text-align: center;
  color: #292929;
  font-size: 2.5em;
  margin-top: 20px;
}
.ChooseClassLine {
  text-align: center;
}
.ChooseClassBox {
  width: 90%;
  height: 300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.ChooseClassBox_DIV {
  /* transition-duration: .5s; */
  border: 1px solid;
  width: 20%;
  height: 80%;
  border-radius: 10px;
  background-image: url(../public/Img/amumamum.PNG);
  background-position: top;
  background-size: 70%;
  background-repeat: no-repeat;
  opacity: 0.5;
  text-align: center;
  font-size: 30px;
  line-height: 400px;
}
@media screen and (min-width: 1920px) {
  .ChooseClass {
    left: 23%;
  }
}
.ChooseClassBox_DIV:hover {
  /* transition-duration: .5s; */
  opacity: 1;
}
.PasswordLine,
.EmailLine {
  display: flex;
  justify-content: space-between;
  margin: 30px 100px 10px 100px;
  border-bottom: 1px dashed #2559bb;
  padding-bottom: 8px;
}
.PasswordLine_Title,
.EmailLine_Title {
  margin-top: 15px;
}
.PasswordBtn,
.EmailBtn {
  margin-top: 70px;
  text-align: center;
}
#submitPassword,
#submitEmail {
  margin-right: 50px;
}
.Classroom {
  display: flex;
  justify-content: space-around;
  height: 350px;
  margin: 50px 150px 10px 150px;
}
.GoStudy,
.GoCheck {
  transition-duration: 0.5s;
  background-image: url(../public/Img/amumamum.PNG);
  opacity: 0.5;
  background-size: 50%;
  background-position: center;
  background-repeat: no-repeat;
  font-size: 2em;
  color: black;
  width: 300px;
  height: 300px;
  text-align: center;
  line-height: 500px;
  border: 1px solid black;
  border-radius: 20px 20px;
}

.GoStudy:hover,
.GoCheck:hover {
  transition-duration: 0.5s;
  opacity: 1;
  width: 350px;
  margin-left: -20px;
  height: 350px;
  line-height: 600px;
  border: 1px dashed black;
}
@keyframes typingProfile {
  from{
    width: 0;
  }
  80%{
    width:11em;
  }
  to {
    width: 12.5em;
  }
}

@keyframes sildeProfile {
  0% {
    height: 0;
  }
  10% {
    height: 200px;
  }
  60% {
    height: 500px;
  }
  100% {
    height: 600px;
  }
}
